@import '../../../../styleNone.scss';

body{
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.post{
    margin-top: 10px;
    border: 2px solid teal;
    min-height: 60px;
    max-width: 500px;
    min-width: 500px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
}
.post__content div{
    max-width: 250px;
    word-wrap: break-word;
}
.pagesBtn{
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    max-width: 500px;
}
.pageBtn{
    border: 1px solid teal;
    margin: 10px;
    width: 30px;
    text-align: center;
    user-select: none;
    cursor: pointer;
}
.page__current{
    background-color: teal;
    color: #eee;
}

form{
    display: flex;
    flex-direction: column;
    padding: 5px;
}

@media (max-width: 500px) {
    .post{
        min-width: 90vw;
        max-width: 90vw;
    }
}