.loader{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 25px dashed rgb(255, 235, 17);
    background-color: rgb(29, 29, 29);
    margin-top: 50px;
    animation: rotate 1.8s infinite linear;
}

@keyframes rotate {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}